.no-js .main-header {
  height: auto;
}
.no-js .main-nav__list {
  position: relative;
}
.no-js .toggle {
  display: none;
}
.no-js .main-nav {
  display: block;
}
.page__body {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  width: 320px;
  text-align: center;
  background-color: #f2f2f2;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0 auto;
}
